html, body {
  width: 100%;
  height: auto;
  min-height: 100%; }

body {
  background-image: url("../img/patterns/symphony.png");
  background-size: 30% auto; }

@font-face {
  font-family: "Garamond";
  src: url("../fonts/GARA.TTF"); }

h2, h1 {
  /*text-transform: capitalize;*/ }

h1:first-letter, h2:first-letter {
  text-transform: uppercase; }

.btn {
  margin-right: 5px; }

.text_easy_read {
  /*white-space: pre-wrap;*/
  max-width: 550px;
  font-size: 16px;
  line-height: 2em;
  /*font-family: Garamond;*/ }

/*
	==============================================
		App wrapper
   	==============================================
*/
#app_wrapper {
  max-width: 980px;
  min-height: 600px;
  margin: 0 auto;
  background-color: rgba(234, 235, 238, 0.67);
  border: 1px solid #e3e3e3;
  padding: 10px; }

/*
	==============================================
		Banner
   	==============================================
*/
#app_banner {
  position: relative;
  background-color: #8ea9ce;
  height: 124px;
  width: 100%;
  margin: 0;
  padding: 0;
  border-bottom: 2px solid #453f3f;
  border-top: 2px solid white; }
  #app_banner .white_stroke {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(250, 250, 250, 0.04);
    width: 100%;
    height: 50%; }
  #app_banner .logo {
    display: inline-block;
    background-image: url(../img/decoration/logo.svg);
    background-repeat: no-repeat;
    width: 120px;
    height: 120px;
    background-size: cover; }
  #app_banner .logotext {
    font-size: 50px;
    color: #1e1c20;
    font-family: Garamond;
    line-height: 120px;
    display: inline-block;
    position: relative;
    top: -40px;
    left: 5px;
    text-transform: uppercase;
    text-shadow: 0px 0px 10px white; }
  #app_banner #user_section {
    position: absolute;
    right: 10px;
    bottom: 10px;
    text-align: right; }
    #app_banner #user_section .glyphicon {
      cursor: pointer;
      display: none; }
    #app_banner #user_section .loginBt {
      background-color: rgba(0, 0, 0, 0.8);
      padding: 5px 10px;
      color: white;
